import { Button, Chip, Container, InputAdornment, Menu, MenuItem, Stack, TextField, Typography } from '@mui/material';
import { Helmet } from 'react-helmet-async';
import { useSettingsContext } from 'src/components/settings';
import { color, decryptToken, formatDate, formatObj } from 'src/utils/common';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { useState, useEffect, useDebugValue } from 'react';
import { constants } from 'src/utils/constant';
import axios, { endpoints } from 'src/utils/axios';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import SearchIcon from '@mui/icons-material/Search';
// sections

// ----------------------------------------------------------------------

export default function SalesReport() {
    const settings = useSettingsContext();
    const [isLoading, setIsLoading] = useState(false)
    const [salesList, setSalesList] = useState([]);
    const [tempData, setTempData] = useState([]);
    const [searchQuery, setSearchQuery] = useState('');
    const { enqueueSnackbar } = useSnackbar();
    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);
    const fetchSales = async () => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.sales.list, {}, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setIsLoading(false);
                setSalesList(response.data.data);
                setTempData(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch sales orders!', { variant: 'error' });
            setIsLoading(false);
        }
    };

    useEffect(() => {
        setIsLoading(true);
        fetchSales();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);


    const columns = [
        {
            name: 'Customer Name',
            selector: row => row.customerId.name,
            sortable: 'true',
        },
        {
            name: 'Invoice Number',
            selector: row => row.invoiceNumber,
        },
        {
            name: 'Amount',
            selector: row => row.totalAmount.toFixed(2),
            sortable: 'true',
        },
        {
            name: 'Payment Mode',
            selector: row => row.payments[0].paymentID.paymentMode,
            sortable: 'true',
        },
        {
            name: 'Invoice Date',
            selector: row => new Date(row.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
            sortable: 'true'
        },
    ];

    const filteredSales = salesList.filter((salesData) =>
        salesData?.customerId.name?.toLowerCase().includes(searchQuery?.toLowerCase())
    );

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Helmet>
                <title> Reports: Sales Report</title>
            </Helmet>

            <Typography variant="h4"> Sales Report </Typography>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between", marginTop: "20px" }}>
                <Stack direction="row" spacing={1}>
                    <Chip
                        label={`Total Amount : ${salesList.length > 0 ? formatObj.format(salesList.reduce((accumulator, currentValue) => accumulator + currentValue.totalAmount, 0)) : formatObj.format(0)}`}
                        variant="outlined"
                    />
                </Stack>

                <Stack>
                    <Button style={{ color: color.actionButton }} endIcon={<ArrowDropDownIcon />} variant='outlined' onClick={(e) => setAnchorEl(e.currentTarget)}>
                        ACTIONS
                    </Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >

                        <MenuItem onClick={() => {
                        }}>Print</MenuItem>

                        <MenuItem onClick={() => {
                        }}>Download</MenuItem>

                    </Menu>
                </Stack>
            </div>

            <div style={{ display: "flex", marginTop: "8px" }}>
                <TextField
                    placeholder='Search'
                    size='small'
                    value={searchQuery}
                    onChange={e => setSearchQuery(e.target.value)}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
            </div>
            <div style={{ marginTop: "20px" }}>
                <DataTable
                    columns={columns}
                    data={filteredSales}
                    pagination
                    progressPending={isLoading}
                />
            </div>
        </Container>
    );
}
